import {Card, Form, Input, Button, Row, Typography, Col } from 'antd'
import {UserOutlined, LockOutlined} from '@ant-design/icons'
import React, {useState} from 'react'
import {NavBarStatus} from '../../model/model'
import Router from 'next/router'
import { BigBlackButton } from '../General/BigBlackButton'

interface LoginComponentProps {
    isSignUp: boolean
    onFinish: (values) => void
    title: string
}

const LoginComponent: React.FC<LoginComponentProps> = (props) => {
    const {isSignUp, onFinish, title} = props

    const onClick = () => {
        window.analytics.track('Go to Sign Up');
        Router.replace('/signup')
    }

    return (
        <div style={{display: 'flex', flexDirection: 'column'}}>
            <Card style={{paddingLeft: 20, paddingRight: 20}} title={<img height={43} src={require('../../assets/sumpixel-logo.png')}/>}>
                <Form
                    name="basic"
                    initialValues={{ remember: true }}
                    onFinish={onFinish}
                >

                    {isSignUp ? 
                        
                            <Input.Group>
                                <Row>
                                    <Col span={11}>
                                        <Form.Item
                                            label="First Name"
                                            name="firstName"
                                            rules={[{ required: true, message: 'First Name is required' }]}
                                        >
                                            <Input placeholder="First Name"/>
                                        </Form.Item>
                                    </Col>
                                    <Col span={2}/>
                                    <Col span={11}>
                                        <Form.Item
                                                label="Last Name"
                                                name="lastName"
                                                rules={[{ required: true, message: 'Last Name is required' }]}
                                            >
                                            <Input placeholder="Last Name"/>
                                        </Form.Item>
                                    </Col>
                                </Row>
                            </Input.Group>
                        
                    : null}

                    <Form.Item
                        label="Email"
                        name="email"
                        rules={[{ required: true, message: 'Email is required' }]}
                    >
                        <Input placeholder="Email"/>
                    </Form.Item>

                    <Form.Item
                        label="Password"
                        name="password"
                        rules={[{ required: true, message: 'Password is required' }]}
                    >
                        <Input type="password" placeholder="Password"/>
                    </Form.Item>

                    <Form.Item>
                        <Row justify="center">
                            <BigBlackButton style={{width: '60%', marginTop: 60}} type="primary" htmlType="submit">
                                {title}
                            </BigBlackButton>
                        </Row>
                    </Form.Item>
                </Form>
            </Card>
            {!isSignUp ? 
                <Typography.Text style={{fontFamily: 'Mark Pro', fontWeight: 500, paddingTop: 10, textAlign: 'center'}}>
                    Don't have an account? <Button onClick={onClick} style={{padding: 0}} type="link">Sign Up.</Button>
                </Typography.Text>
            : null}
        </div>
    )
}

export default LoginComponent